#site-footer {
    display: flex;
    background: $drk_grey;
    color: $white;
    padding: 30px 20% 44px;
    justify-content: space-between;

    .site-branding {
        filter: brightness(0) invert(1);
    }
}

#quick-links {
    display: flex;
    flex-direction: column;
    
    a {
        color: $white;
        text-decoration: none;
    }
}